.todo-item {
  border-top: 1px solid rgba(0, 0, 0, 0.1); }
  .todo-item button:first-child {
    float: left; }
  .todo-item button:last-child {
    float: right; }
  .todo-item .title {
    float: left;
    display: block;
    max-width: 70%;
    overflow-x: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    font-size: 16px;
    line-height: 2em;
    padding-left: 10px; }
    .todo-item .title.completed {
      text-decoration: line-through;
      opacity: 0.7; }



